import React, { useState } from 'react'

export default function TextBox(props) {
    const [text, setText] = useState("");

    function uppercaseClick() {

        var newText = text.toUpperCase();
        setText(newText);
        props.showAlert("Converted to Upper Case !","success");
    }
    
    
    function lowercaseClick() {
        
        var newText = text.toLowerCase();
        setText(newText);
        props.showAlert("Converted to Lower Case !","success");
    }
    
    
    function clearClick() {
        
        var newText = "";
        setText(newText);
        props.showAlert("Cleared all text !","success");
    }
    
    
    function copyClick() {
        
        var newText = document.getElementById("myBox");
        newText.select();
        navigator.clipboard.writeText(newText.value);
        props.showAlert("All items Copied on clipboard !","success");
        
    }
    
    
    function extaSpacesClick() {
        
        var newText = text.split(/[ ]+/);
        setText(newText.join(" "));
        props.showAlert("All Extra spaces removed !","success");
        

    }


    // toggleClick = () => {
    //     let a = text.toLowerCase()
    //     if (a = text) {
    //         var upText = text.toUpperCase();
    //         setText(upText);
    //     }
    //     else {
    //         let newText = text.tolowerCase();
    //         setText(newText);
    //     }
    // }

    function handleOnChange(event) {

        setText(event.target.value);
    }
    return (
        <>
            <div className="form-floating">
                <div className="mb-3 my-4">
                    <label htmlFor="myBox" className="form-label" style={{color: props.mode==="dark"?"white":"#042743"  }} ><h2><b>{props.heading}</b></h2></label>
                    <textarea className="form-control" value={text} id="myBox" rows="10" onChange={handleOnChange} style={{backgroundColor: props.mode==="dark"?"#042743":"white",color:props.mode==="dark"?"white":"#042743" }}></textarea>
                </div>
                <button className="btn btn-primary my-2 mx-2" onClick={uppercaseClick} >Change to Upper-Case</button>
                <button className="btn btn-primary my-2 mx-2" onClick={lowercaseClick} >Change to Lower-Case</button>
                <button className="btn btn-primary my-2 mx-2" onClick={clearClick} >Clear</button>
                <button className="btn btn-primary my-2 mx-2" onClick={copyClick} >Copy Text</button>
                <button className="btn btn-primary my-2 mx-2" onClick={extaSpacesClick} >Remove Extra Spaces</button>
                {/* <button className="btn btn-primary my-2 mx-2" onClick={toggleClick} >Toggle-Case</button> */}
            </div>
            <div className="container my-4" style={{color: props.mode==="dark"?"white":"#042743"  }}>
                <h3>Text Summary</h3>
                <p>{text.split(" ").length} words and {text.length} characters</p>
                <p>{0.008 * text.split(" ").length} Minutes will take to read this</p>
                <h3>Preview</h3>
                <p>{text.length>0?text:"Write something in Textbox Above to Preview it !"}</p>

            </div>
        </>
    )
}
